@import "./utils";

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    
}

body {
    background-color: $color-green;
}


.repo {
    position: fixed;
    top: 0;
    left: 3rem;
    z-index: 10;
    padding: 10px;
    color: #fff;
    font-size: 15px;
    background-color: rgba(0, 0, 0, 0.5);
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
  }
  .repo a > span > i {
    font-size: 30px;
    color: #fff;
    margin-left: 5px;
  }  
 
