@import "./utils";

.card {
    background-color: transparent;
    width: 300px;
    height: 300px;
    perspective: 1000px;
    margin: 1.5rem;
   

    &--inner{
        position: relative;
        width: 100%;
        height: 100%;
        text-align: center;
        transition: transform 0.6s;
        transform-style: preserve-3d;
        box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
        border-radius: 20px;
    }
    &:hover &--inner {
        transform: rotateY(180deg);
    }
    &--front, &--back {
        position: absolute;
        width: 100%;
        height: 100%;
        -webkit-backface-visibility: hidden;
        backface-visibility: hidden; 
    }
    
    &--back{
        p{
            font-weight: 600;
        }
        span {
            font-weight: 400;
        }
        
        padding: 1rem;
        background-color: $color-rose;
        color: grey;
        transform: rotateY(180deg);
        border-radius: 20px;
        display: flex;
        flex-direction: column;
        justify-content: space-evenly;
        align-items: center;
    
    }
    &--front {
        
        img {
           width: 100%;
            height:100%;
            border-radius: 20px;
        }
        .tag {
            position: relative;
            bottom: 15%;
            margin: 1rem;
            border-radius: 10px;

            &--new-recipe{
                background-color: $color-red;
                color: white;
                padding: .5rem;
                
            }
            &--no-sugar{
                background-color: $color-violet;
                color: white;
                padding: .5rem;
                
            }
        }
    }

}